@import url(https://fonts.googleapis.com/css?family=Nunito:300);
.data-content{
    padding: 20px;
    text-align: left;
    font-size: 18px;
}
.data-image-content {
    float: right;
    width: 80px;
    height: 80px;
    margin-right: 60px;
    border-radius: 50%;
}

a, a:link, a:visited, a.hover, a.active{
    text-decoration: none;
    color: #000000;
}
.root-teamlist{
    -webkit-transition: all 0.6s;
    transition: all 0.6s;
    /* border-radius: 25px; */
    border-bottom: 1px solid #ffffff;
    display: 'flex';
    flex-wrap: 'wrap';
    align-items: 'center';
    justify-content: 'center';
    cursor: 'pointer';
    margin: 25px;
    padding: 15px;
}
.root-teamlist:hover{
    /* background-color: #888888; */
    border-radius: 25px;
    border-bottom: 1px solid #309afc!important;

}
body {
  text-align: center;
}
.home-heading{
  font-size: 25px;
  margin-bottom:30px;
}

.MuiPaper-root{
  width : 700px;
  margin: auto;
  padding: 30px;
  min-width: 420px;
  min-height: 300px;
}

.Api-Error, .Home-Loader{
  margin: '0 auto';
  margin-top:10%
}
.MuiInputBase-root{
    width : 350px;
}

.MuiPaper-root{
    width : 700px;
    margin: auto;
    padding: 30px;
    min-width: 420px;
}

.MuiButtonBase-root{
    margin-top: 15px!important;
    width : 150px;
    margin-left: 200px!important;
    background-color: #309afc!important;
    background-image: none!important;
    font-size: 13px!important;
    border: 1px solid #0084e8!important;
    border-radius: 1px !important;
    box-shadow: none!important;
    text-shadow: none!important;
}

.MuiButtonBase-root:hover{
    background-color: #0084e8!important;
}

.repoInputs{
    padding: 10px;
}

.back-button{
    position: fixed;
    left:25px;
    top:20px;
    font-size: 35px!important;
    cursor: pointer;
    border: 2px solid #309afc;
    border-radius: "50%"!important;
    color: #309afc;
    -webkit-transition: all 0.5s!important;
    transition: all 0.5s!important;
}
.back-button:hover{
    background-color: #309afc;
    color: #ffffff;
}

.statusMessage{
    color: aquamarine;
    display: 'inline-block'
}

.errorMessage{
    color: chocolate;
    display: 'inline-block'
}

.messages-panel{
    overflow: scroll;
    max-height: 150px;
    text-align: left;
    margin-left: 20px;
}
body { font-family: "Nunito" }
